/* View Content */
.sf-view-content {
  padding: 1.5rem;
}

.sf-view-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.sf-view-grid h4 {
  margin: 0 0 1rem;
  font-size: 1.1rem;
  font-weight: 600;
  color: #2d3748;
}

.sf-info-box {
  background: #f7fafc;
  border-radius: 8px;
  padding: 1.5rem;
}

.sf-info-title {
  font-weight: 500;
  margin-bottom: 0.75rem;
  font-size: 1.1rem;
  color: #2d3748;
}

.sf-info-box p {
  margin: 0.5rem 0;
  font-size: 0.9rem;
}

