.installment-section {
  margin-bottom: 2rem;
}

.installment-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: #2d3748;
  margin: 0 0 1rem;
}

.installment-box {
  background: #f7fafc;
  border-radius: 8px;
  padding: 1.5rem;
  overflow-x: auto;
}

.installment-table {
  width: 100%;
  border-collapse: collapse;
}

.installment-table th {
  background: #e2e8f0;
  padding: 0.75rem;
  text-align: left;
  font-weight: 600;
  color: #4a5568;
  border-bottom: 1px solid #cbd5e0;
}

.installment-table td {
  padding: 0.75rem;
  border-bottom: 1px solid #e2e8f0;
}

.installment-table tr:last-child td {
  border-bottom: none;
}

/* Payments Table */
.sf-payments-table {
  width: 100%;
  border-collapse: collapse;
}

.sf-payments-table th {
  background: #e2e8f0;
  padding: 0.75rem;
  text-align: left;
  font-weight: 600;
  color: #4a5568;
  border-bottom: 1px solid #cbd5e0;
}

.sf-payments-table td {
  padding: 0.75rem;
  border-bottom: 1px solid #e2e8f0;
}

.sf-payments-table tr:last-child td {
  border-bottom: none;
}

/* Responsive Design */
@media (max-width: 768px) {
  .sf-header-content {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
  
  .sf-table-header {
    flex-direction: column;
    gap: 1rem;
    align-items: flex-start;
  }
  
  .sf-table-search {
    width: 100%;
  }
  
